import React from "react";
import { useEffect, useState } from "react";
import { ThemeProvider } from "styled-components";

interface Theme {
    background: string;
    body: string;
    border: string;
    copyButton: string;
    copyButtonPressed: string;
    disabledQRCode: string;
    errorText: string;
    hintText: string;
    inputBackground: string;
    inputBackgroundSelected: string;
    inputBorder: string;
    inputBorderSelected: string;
    link: string;
    linkVisited: string;
    outputBackground: string;
    text: string;
}

const themeLight = {
    background: "#FDFDFA",
    body: "#9f9993",
    border: "#EEEEEE",
    copyButton: "rgb(255, 251, 211)",
    copyButtonPressed: "rgb(235, 231, 191)",
    disabledQRCode: "lightgrey",
    errorText: "#CC8888",
    hintText: "#888888",
    inputBackground: "#FAFAFA",
    inputBackgroundSelected: "white",
    inputBorder: "#CCCCCC",
    inputBorderSelected: "#4286f4",
    link: "", // default platform color
    linkVisited: "", // default platform color
    outputBackground: "#E6DB74",
    text: "black",
};

const themeDark = {
    background: "#292824",
    body: "#1b1a18",
    border: "#514f48",
    copyButton: "#827717",
    copyButtonPressed: "#c3b322",
    disabledQRCode: "#514f48",
    errorText: "#f26363",
    hintText: "#b3b3b3",
    inputBackground: "#44423c",
    inputBackgroundSelected: "#514f48",
    inputBorder: "#CCCCCC",
    inputBorderSelected: "#4286f4",
    link: "#b366ff",
    linkVisited: "#b366ff",
    outputBackground: "#6c6313",
    text: "#eeeeee",
};

function getPreferredColorScheme(): Theme {
    if (window.matchMedia) {
        if (window.matchMedia("(prefers-color-scheme: dark)").matches) {
            return themeDark;
        } else {
            return themeLight;
        }
    }
    return themeDark;
}

export function useTheme(): Theme {
    const [theme, setTheme] = useState(getPreferredColorScheme);

    useEffect(() => {
        if (window.matchMedia) {
            const onChange = () => {
                setTheme(getPreferredColorScheme());
            };

            const colorSchemeQuery = window.matchMedia("(prefers-color-scheme: dark)");
            colorSchemeQuery.addListener(onChange);

            return () => colorSchemeQuery.removeListener(onChange);
        }

        return undefined;
    });

    return theme;
}

export function AutoThemeProvider({ children }: any): JSX.Element {
    const theme = useTheme();

    document.body.style.background = theme.body;

    return <ThemeProvider theme={theme}>{children}</ThemeProvider>;
}
