import React from "react";
import styled from "styled-components";
import { FormSectionStyle, DescriptionStyle, WarningStyle } from "./styles";

// TODO how to make mixins work so this can be imported from input.tsx?
const FakeInput = styled.button`
    color: ${(props) => props.theme.text};
    font-size: 1em;
    padding: 0.5em;
    border-radius: 0.3em;
    float: right;
    border-style: dotted;
    border-color: ${(props) => props.theme.inputBorder};
    border-width: 0.1em;
    background-color: ${(props) => props.theme.inputBackground};
    min-width: 14.7em;
    text-align: left;
    /* what the hell is this number? Why is it 1.2 on inputs and 2.6 here?? */
    max-height: 2.5em;

    &:focus {
        background-color: ${(props) => props.theme.inputBackgroundSelected};
        border-color: ${(props) => props.theme.inputBorderSelected};
        outline: none;
    }
`;

const InvisiblePicker = styled.input`
    display: none;
`;

function validateFile(e: React.ChangeEvent<HTMLInputElement>): File | undefined {
    if (!e.target.files || e.target.files.length === 0) {
        return;
    }

    if (e.target.files.length > 1) {
        alert("Select only one file");
        return;
    }
    return e.target.files[0];
}

interface UIFileComponentProps {
    onFilePicked: (name: File | undefined) => void;
}

export function UIFileComponent({ onFilePicked }: UIFileComponentProps): JSX.Element {
    const filePicker = React.useRef<HTMLInputElement>(null);
    const [selectedFile, setSelectedFile] = React.useState<File | undefined>();

    const onClick = () => {
        if (filePicker.current) {
            filePicker.current.click();
        }
    };

    const onFileSelected = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = validateFile(e);

        onFilePicked(file);
        setSelectedFile(file);
    };

    const fileFieldContent = (): string => {
        // place a bunch of dots in the out field
        if (selectedFile) {
            return new Array(selectedFile.name.length + 1).join("");
        }
        return "No file selected";
    };

    const renderOkText = (): string => {
        return selectedFile ? " " : "";
    };

    return (
        <FormSectionStyle>
            <DescriptionStyle>
                <span>Choose your key file{renderOkText()}</span>
                <br />
                <WarningStyle>
                    WARNING: you will lose your passwords losing this file, or changing it in any
                    way!
                </WarningStyle>
            </DescriptionStyle>
            <InvisiblePicker type="file" ref={filePicker} onChange={onFileSelected} />
            <FakeInput onClick={onClick}>
                <b>{fileFieldContent()}</b>
            </FakeInput>
        </FormSectionStyle>
    );
}
