import "react-toggle/style.css";

import React from "react";
import { useState, useEffect } from "react";
import Toggle from "react-toggle";
import styled from "styled-components";
import { UIDomainInput } from "./domain_component";
import { UIFileComponent } from "./file_component";
import { UIOutDiv } from "./outdiv";
import { UIPassPhraseInput } from "./passphrase_input";
import { FormSectionStyle, DescriptionStyle, InputStyle, ToggleContainerStyle } from "./styles";
import { Util } from "./util";

declare const COMMITHASH: string;

const SECOND = 1000;
const MINUTE = 60 * SECOND;
const TIMEOUT_MS = 5 * MINUTE;

const ContainerStyle = styled.div`
    color: ${(props) => props.theme.text};
    margin: 0 auto;
    max-width: 35em;
    outline-style: solid;
    outline-width: 1px;
    outline-color: ${(props) => props.theme.border};
    padding: 0.5em;

    /* padding for the iPhone x bottom bar */
    padding-bottom: 30px;
    background-color: ${(props) => props.theme.background};
`;

const CommitHashStyle = styled.span`
    font-size: 0.85em;
    color: grey;
    font-style: italic;
`;

const Link = styled.a`
    color: ${(props) => props.theme.link};
`;

function isCorrectCharset(
    password: string,
    includeLowercase: boolean,
    includeNumbers: boolean,
    includeSymbols: boolean,
    includeUppercase: boolean
) {
    let hasNumber = false;
    let hasSymbol = false;
    let hasUppercase = false;
    let hasLowercase = false;

    for (let i = password.length - 1; i >= 0; i--) {
        const c = password[i];

        if (c >= "a" && c <= "z") {
            hasLowercase = true;
        } else if (c >= "A" && c <= "Z") {
            hasUppercase = true;
        } else if (c >= "0" && c <= "9") {
            hasNumber = true;
        } else {
            hasSymbol = true;
        }
    }

    return (
        hasLowercase === includeLowercase &&
        hasNumber === includeNumbers &&
        hasSymbol === includeSymbols &&
        hasUppercase === includeUppercase
    );
}

let cachedFile: File | undefined;
let cachedFileContent: string | undefined;
let generateVersion = 0;

function ensureGenerationCurrent(version: number): void {
    if (version !== generateVersion) {
        throw new Error("Calculation aborted by starting a new one");
    }
}

async function startGeneration(
    file: File,
    domain: string,
    phrase: string,
    passLength: number,
    requireSymbols: boolean
): Promise<string> {
    const currentVersion = ++generateVersion;

    if (file !== cachedFile || !cachedFileContent) {
        cachedFileContent = (await Util.asyncReadFileAsBinaryString(file)) as string;
        cachedFile = file;

        ensureGenerationCurrent(currentVersion);
    }

    let buffer = cachedFileContent.slice();

    // hack?
    const crypto = window.crypto.subtle;

    // append the domain
    buffer += domain;
    // append the phrase if any
    if (phrase) {
        buffer += phrase;
    }

    // run this loop while this calculation is still current
    while (true) {
        const raw = new TextEncoder().encode(buffer);

        const value = await crypto.digest("SHA-512", raw);

        ensureGenerationCurrent(currentVersion);

        let base64String = btoa(String.fromCharCode(...new Uint8Array(value))).substring(
            0,
            passLength
        );

        // check if we good. If not, run append the current password
        // to the end of the blob, and restart
        if (isCorrectCharset(base64String, true, true, requireSymbols, true)) {
            // the default / and + are really bad choices for passwords and they are generally
            // unsupported. Replace them with - and ! instead
            base64String = base64String.replace(/\//g, "-");
            base64String = base64String.replace(/\+/g, "!");

            return base64String;
        } else {
            buffer += base64String;
        }
    }
}

function useGeneratedPassword(
    file: File | undefined,
    domain: string | undefined,
    phrase: string | undefined,
    passLength: number,
    requireSymbols: boolean
): [string | undefined, string | undefined] {
    const [pass, setPass] = useState("Loading");

    if (!domain) {
        return [undefined, "Pick a domain"];
    }
    if (!file) {
        return [undefined, "Pick a file"];
    }
    if (phrase === undefined) {
        return [undefined, "Pick a valid passphrase"];
    }
    if (!passLength) {
        return [undefined, "Pick a valid password length"];
    }

    startGeneration(file, domain, phrase, passLength, requireSymbols).then(setPass);

    return [pass, undefined];
}

export function UIRoot(): JSX.Element {
    const [file, setFile] = useState<File | undefined>();
    const [domain, setDomain] = useState<string | undefined>();
    const [phrase, setPhrase] = useState<string | undefined>();
    const [requireSymbols, setRequireSymbols] = useState(true);
    const [passLength, setPasswordLength] = useState(20);
    const [password, generateError] = useGeneratedPassword(
        file,
        domain,
        phrase,
        passLength,
        requireSymbols
    );
    const [baseId, setBaseId] = useState(0);

    const onPassPhraseSelected = (phrase: string, usable: boolean) => {
        setPhrase(usable ? phrase : undefined);
    };

    const onSymbolsAllowedUpdated = (e: React.ChangeEvent<HTMLInputElement>) => {
        setRequireSymbols(e.target.checked);
    };

    const onPasswordLengthChanged = (e: React.ChangeEvent<HTMLInputElement>) => {
        setPasswordLength(parseInt(e.currentTarget.value, 10));
    };

    const onPasswordExpiration = () => {
        // reset sensitive inputs so unattended devices don't leak informations
        setFile(undefined);
        setPhrase(undefined);

        // increment a versioning number that ensures that sensitive fields are fully rebuild
        setBaseId(baseId + 10);
    };

    // set a timeout to reset the password after TIMEOUT_MS
    useEffect(() => {
        if (password) {
            const tk = setTimeout(onPasswordExpiration, TIMEOUT_MS);

            return () => clearTimeout(tk);
        }
        return;
    });

    const expirationTime = new Date().getTime() + TIMEOUT_MS;

    if (location.protocol !== "https:") {
        // crypto will not work
        return (
            <ContainerStyle>
                <h1>Make password, yes?</h1>
                <hr />
                Error: PASSMAKER only works in secure (HTTPS) contexts. Please reload!
            </ContainerStyle>
        );
    }

    return (
        <ContainerStyle>
            <h1>Make password, yes?</h1>
            <hr />
            <UIDomainInput onDomainPicked={setDomain} />
            <UIFileComponent key={baseId + 0} onFilePicked={setFile} />
            <UIPassPhraseInput key={baseId + 1} onPassPhraseSelected={onPassPhraseSelected} />
            <FormSectionStyle>
                <DescriptionStyle>Password length</DescriptionStyle>
                <InputStyle
                    type="number"
                    defaultValue={passLength}
                    onChange={onPasswordLengthChanged}
                />
            </FormSectionStyle>
            <FormSectionStyle>
                <DescriptionStyle>With - and !</DescriptionStyle>
                <ToggleContainerStyle>
                    <Toggle
                        defaultChecked={true}
                        icons={false}
                        onChange={onSymbolsAllowedUpdated}
                    />
                </ToggleContainerStyle>
            </FormSectionStyle>
            <UIOutDiv
                password={password}
                generateTip={generateError}
                expirationTime={expirationTime}
            />
            <CommitHashStyle>
                Version {COMMITHASH} - <Link href="old/index.html">go to old version</Link>
            </CommitHashStyle>
        </ContainerStyle>
    );
}
