import React from "react";
import styled from "styled-components";
import EyeIcon from "./images/eye.svg";
import { Util } from "./util";
import { InputStyle, FormSectionStyle, DescriptionStyle, HintStyle } from "./styles";

declare class UIPassPhraseInputProps {
    public onPassPhraseSelected: (phrase: string, usable: boolean) => void;
}

const MIN_PASSPHRASE_LENGTH = 5;

interface IconProps {
    src: string;
}

const Icon = styled.div<IconProps>`
    background: ${(props) => props.theme.hintText};
    width: 25px;
    height: 25px;
    margin: 5px;
    mask: url(${(props) => props.src}) no-repeat;
    mask-size: contain;
`;

const RevealButton = styled.button`
    position: absolute;
    right: 0px;
    background: 0;
    border: 0;

    &:disabled {
        opacity: 0.25;
    }
`;

const InputAndRevealContainer = styled.div`
    position: relative;
    float: right;
`;

export function UIPassPhraseInput({ onPassPhraseSelected }: UIPassPhraseInputProps): JSX.Element {
    const [selectedPhrase, setSelectedPhrase] = React.useState<string | undefined>();
    const [reveal, setReveal] = React.useState(false);

    const okText = (): string => {
        const len = selectedPhrase?.length ?? 0;
        const ok = len >= MIN_PASSPHRASE_LENGTH;

        return ok ? " " : "";
    };

    const hintText = (): string | null => {
        if (selectedPhrase) {
            if (selectedPhrase.length < MIN_PASSPHRASE_LENGTH) {
                return "Too short. At least 5 characters";
            }
            return null;
        }
        return "(optional) protect your key file";
    };

    const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const usable =
            e.target.value.length >= MIN_PASSPHRASE_LENGTH || e.target.value.length === 0;

        onPassPhraseSelected(e.target.value, usable);
        setSelectedPhrase(e.target.value);
    };

    const onReveal = async () => {
        setReveal(true);

        await Util.asyncTimeout(2000);

        setReveal(false);
    };

    const canReveal = selectedPhrase?.length && !reveal;

    return (
        <FormSectionStyle>
            <DescriptionStyle>
                Passphrase{okText()}
                <br />
                <HintStyle>{hintText()}</HintStyle>
            </DescriptionStyle>
            <InputAndRevealContainer>
                <InputStyle
                    autoComplete="off"
                    autoCorrect="off"
                    autoCapitalize="off"
                    spellCheck={false}
                    type={reveal ? "text" : "password"}
                    onChange={onChange}
                />
                <RevealButton onClick={onReveal} disabled={!canReveal}>
                    <Icon src={EyeIcon} />
                </RevealButton>
            </InputAndRevealContainer>
        </FormSectionStyle>
    );
}
