import "./styles/main.css";
import React from "react";
import { createRoot } from "react-dom/client";
import { AutoThemeProvider } from "./theme";
import { UIRoot } from "./ui_root";

// Install PWA offline ServiceWorkers
if ("serviceWorker" in navigator) {
    window.addEventListener("load", () => {
        navigator.serviceWorker
            .register("./service-worker.js")
            .then((registration) => {
                console.log("SW registered: ", registration);
            })
            .catch((registrationError) => {
                console.log("SW registration failed: ", registrationError);
            });
    });
}

// disable that weird 8px margin forced on the body
document.body.style.margin = "0px";

window.addEventListener("load", () => {
    const reactDiv = document.body.appendChild(document.createElement("div"));
    const root = createRoot(reactDiv);

    root.render(
        <AutoThemeProvider>
            <UIRoot />
        </AutoThemeProvider>
    );
});
