import React from "react";

export class Util {
    public static findMostCommon<T>(arr: T[]): T | undefined {
        // first sort the vector to create continuous runs
        arr.sort();

        let best;
        let bestRunLength = 0;
        let runLength = 0;
        for (let i = 0; i < arr.length; ++i) {
            const cur = arr[i];
            if (i > 0 && cur !== arr[i - 1]) {
                // run has ended. Check if it's the best
                if (runLength > bestRunLength) {
                    bestRunLength = runLength;
                    best = cur;
                }

                runLength = 0;
            } else {
                ++runLength;
            }
        }
        // handle the last run
        if (runLength > bestRunLength) {
            bestRunLength = runLength;
            best = arr[arr.length - 1];
        }

        return best;
    }

    public static readFile(file: File, callback: (content: string) => void): void {
        const reader = new FileReader();
        reader.onload = () => {
            if (typeof reader.result === "string") {
                callback(reader.result);
            }
        };

        reader.readAsDataURL(file);
    }

    public static asyncTimeout(ms: number): Promise<void> {
        return new Promise((resolve) => setTimeout(resolve, ms));
    }

    public static rangeRandom(min: number, max: number): number {
        return Math.random() * (max - min) + min;
    }

    public static randomInt(min: number, max: number): number {
        return Math.floor(this.rangeRandom(min, max));
    }

    public static randomBool(): boolean {
        return Math.random() < 0.5;
    }

    public static playbackEnded(audio: HTMLMediaElement): Promise<void> {
        return new Promise((resolve) => {
            if (audio.ended) {
                resolve();
            }
            audio.addEventListener("ended", () => resolve());
        });
    }

    public static parseBool(text: any): boolean | undefined {
        if (text === "true") {
            return true;
        }
        if (text === "false") {
            return false;
        }
        return undefined;
    }

    public static asyncReadFileAsBinaryString(file: File): Promise<string | ArrayBuffer | null> {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();

            reader.onload = () => {
                resolve(reader.result);
            };

            reader.onerror = reject;

            reader.readAsBinaryString(file);
        });
    }

    public static useToggle(initial: boolean): [boolean, () => void] {
        const [state, setState] = React.useState(initial);

        const toggle = () => {
            setState(!state);
        };

        return [state, toggle];
    }
}
